import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DriverService } from '../services/driver.service';
import { MatDialog } from '@angular/material';
import { DriversFormDialogComponent } from '../drivers-form-dialog/drivers-form-dialog.component';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-drivers-list',
  templateUrl: './drivers-list.component.html',
  styleUrls: ['./drivers-list.component.scss']
})
export class DriversListComponent implements OnInit {

  protected drivers: { header: string[]; itemsContent: any[]; };
  protected isLoadingDrivers: boolean;

  constructor(private driverService: DriverService,
              private router: Router,
              public addDriverDialog: MatDialog) {
                this.isLoadingDrivers = true;
  }

  ngOnInit() {
    this.drivers = {
      header: ['Foto', 'Nome', 'Cpf', 'Cnh', 'Registro'],
      itemsContent: []
    };
    this.showDrivers();
  }

  showDrivers() {
    this.drivers.itemsContent = [];
    this.driverService.driversObs.subscribe((data) => {

      for (const driverData of data) {
          this.drivers.itemsContent.push(driverData);
      }
      this.isLoadingDrivers = false;
    });
  }

  addNewDriver() {
    const loginRef = this.addDriverDialog.open(DriversFormDialogComponent);
    loginRef.afterClosed().subscribe(result => {
      this.showDrivers();
    });
  }

}



